* {margin:0;padding:0;line-height:1;}
button {padding:0;border:0 none;background:transparent;text-align:left;font-size:16px;}
.App {
  display:flex;
  flex-direction:column;
}

.blog-header {background-color:#0ff;}
.blog-header .header__title {
  font-size:24px;
  color:blue;
  text-align: left;
  padding:20px;
}

.blog-content {
  display:flex;
  flex-direction: column;
  align-items:flex-start;
  gap: 10px;
  padding: 0 20px;
}
.itemTitle {
  display:flex;
  align-items: center;
  gap:10px;
}

.blog-content .content__item {
  width: 100%;
  padding: 20px;
  border: 1px solid #090909;
  box-sizing:border-box;
}
.blog-content .content__item:first-child {
  margin-top: 20px;
}
.like-box {display:flex;gap:4px;}
.change-button {height: 40px;background-color:#666fff;padding: 0 20px;color: #fff;font-weight: 700;margin-bottom: 10px;}
.modal {display:flex;flex-direction: column;gap:20px; width: 100%;padding: 50px 20px; border: 1px solid #000;background-color: #f9f9f9;box-sizing: border-box;}
.modal .modal__header {}
.modal .modal__header .modal__title {font-size: 20px;font-weight:700;}
.modal .modal__content {display:flex;flex-direction: column;gap:12px;}
.modal .modal__content p {font-size: 16px; font-weight: 600;}

input[type="text"] {height: 32px;box-sizing:border-box}
.default-button {display: inline-block;padding: 8px 12px;background-color: #233f0f;color: #fff; margin-left: 4px;}